<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-form :model="addForm" :rules="addrules" ref="addFormRef" label-width="100px" label-position="top">
        <el-alert title="添加商品信息" type="info" center :closable="false"> </el-alert>
        <el-steps :active="activeName - 0" finish-status="success" align-center>
          <el-step title="基本信息"></el-step>
          <el-step title="商品参数"></el-step>
          <el-step title="商品属性"></el-step>
          <el-step title="商品图片"></el-step>
          <el-step title="商品内容"></el-step>
          <el-step title="完成"></el-step>
        </el-steps>
        <el-tabs v-model="activeName" tab-position="left" @tab-click="tabclick" :before-leave="check">
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number" min="0"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number" min="0"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number" min="0"></el-input>
            </el-form-item>
            <el-form-item label="商品分类">
              <el-cascader :options="cateAll" :props="prop" v-model="addForm.goods_cat" @change="handleChange"> </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <el-form-item v-for="item in attributes" :key="item.attr_id" :label="item.attr_name">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox v-for="(item1, i) in item.attr_vals" :key="i" :label="item1" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form ref="form">
              <el-form-item v-for="item in only" :label="item.attr_name" :key="item.attr_id">
                <el-input v-model="item.attr_vals"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <el-upload
              class="upload-demo"
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="picObj"
              :on-success="success"
              multiple
            >
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <quill-editor v-model="addForm.goods_introduce" ref="myQuillEditor"> </quill-editor>

            <el-button type="primary" @click="addGoods" class="btnAdd">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <el-dialog title="图片预览" :visible.sync="picdialogVisible" width="50%">
      <img :src="src" style="width:100%" />
      <span slot="footer" class="dialog-footer">
        <el-button @click="picdialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="picdialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data() {
    return {
      activeName: '',
      addrules: {
        goods_name: [{ required: true, message: '请输入商品名称', trigger: 'blur' }],
        goods_price: [{ required: true, message: '请输入商品价格', trigger: 'blur' }],
        goods_number: [{ required: true, message: '请输入商品数量', trigger: 'blur' }],
        goods_weight: [{ required: true, message: '请输入商品重量', trigger: 'blur' }]
      },
      addForm: {
        goods_name: 'xiaohei',
        goods_price: '12',
        goods_number: '12',
        goods_weight: '12',
        pics: [],
        goods_introduce: '',
        goods_cat: '',
        attrs: []
      },
      cateAll: [],
      prop: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      checkList: '',
      //动态参数
      attributes: [],
      //静态属性
      only: [],
      fileList: [],
      picdialogVisible: false,
      src: ''
    }
  },
  created() {
    this.getCate()
  },
  methods: {
    async getCate() {
      let { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取全部分类失败')
      }
      this.cateAll = res.data
      // console.log(this.cateAll)
    },
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) this.addForm.goods_cat = []
      //console.log(this.addForm.goods_cat)
    },
    check(activeName, oldActiveName) {
      //  this.$refs.addFormRef.validate(valid=>{
      //    console.log(valid)}
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请选择分类')
        return false
      }
    },
    async tabclick() {
      if (this.activeName === '1') {
        let { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: {
            sel: 'many'
          }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('获取动态参数失败')
        }
        this.attributes = res.data
        this.attributes.forEach(elm => {
          elm.attr_vals = elm.attr_vals.length === 0 ? [] : elm.attr_vals.split(' ')
        })
        console.log(res)
      }
      if (this.activeName === '1') {
        let { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: {
            sel: 'only'
          }
        })
        if (res.meta.status !== 200) {
          return this.$message.error('获取静态属性失败')
        }
        this.only = res.data
        console.log(this.only)
      }
    },
    handlePreview(file) {
      console.log(file)
      this.picdialogVisible = true
      this.src = file.response.data.url
    },
    //点击图片右上角的移除
    handleRemove(file) {
      // 1. 获取将要删除的图片的临时路径
      const filePath = file.response.data.tmp_path
      // 2. 从 pics 数组中，找到这个图片对应的索引值
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      // 3. 调用数组的 splice 方法，把图片信息对象，从 pics 数组中移除
      this.addForm.pics.splice(i, 1)
      // console.log(this.addForm)
      // if(file.response.data.tmp_path)
      // console.log(file.response.data.tmp_path)
      // file.findIndex(ele=>{
      // })
      console.log(this.addForm.pics)
    },
    success(response) {
      // this.fileList=flielist
      // console.log(response)
      // console.log(flie)
      // console.log(flielist)
      const picInfo = { pic: response.data.tmp_path }
      // 2. 将图片信息对象，push 到pics数组中
      this.addForm.pics.push(picInfo)
    },
    addGoods() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return

        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        this.attributes.forEach(item => {
          const many = {}
          many.attr_id = item.attr_id
          many.attr_vals = item.attr_vals
          console.log(many)
          this.addForm.attrs.push(many)
        })
        this.only.forEach(item => {
          const only = {}
          only.attr_id = item.attr_id
          only.attr_vals = item.attr_vals
          console.log(only)
          this.addForm.attrs.push(only)
        })
        form.attrs = this.addForm.attrs
        console.log(form)
        let { data: res } = await this.$http.post('goods', form)
        console.log(res)
        if (res.meta.status !== 201) {
          return this.$message.error('添加商品失败')
        }
        this.$message.success('添加商品成功')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
    },
    picObj() {
      return { Authorization: window.sessionStorage.getItem('token') }
    }
  }
}
</script>
<style lang="less" scoped>
.el-steps {
  margin: 10px 0;
}
.btnAdd {
  margin-top: 15px;
}
</style>
